body {
  font-family: "Lato", sans-serif;
  background: #f5f5f5;
  background-image: url("https://www.transparenttextures.com/patterns/concrete-wall.png");
}

h1,
h2,
h3 {
  font-weight: 700;
  font-family: "Paytone One", sans-serif;
}

h5 {
  font-weight: 700;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  color: #666;
}

a {
  color: #fd1a16;
}

a:focus,
a:hover,
a:active {
  color: #128f76;
}

.btn {
  border-width: 2px;
}

.text-primary {
  color: #fd1a16 !important;
}

.text-secondary {
  color: #2c3e50 !important;
}

.sub-logo {
  display: block;
  cursor: pointer;
  width: 76px;
  position: absolute;
  z-index: 997;
  top: 0;
  left: 2rem;
}

.menu {
  display: block;
  cursor: pointer;
  width: 76px;
  position: absolute;
  z-index: 999;
  top: 0;
  right: 2rem;
}

nav {
  position: absolute;
  z-index: 998;
  top: 80px;
  right: 3rem;
  text-align: right;
}

nav .nav-link {
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #000;
  transition: all 0.25s ease;
}

nav .nav-link:hover {
  padding-right: 1rem;
}

header {
  padding-top: 5rem;
  padding-bottom: 6rem;
  min-height: 100vh;
}

.profileImg {
  width: 148px;
  margin: 0 auto;
  position: relative;
}

.profileImg img {
  width: 148px;
}

.profileImg .img1 {
  position: absolute;
  z-index: 3;
}

.profileImg .img2 {
  position: absolute;
  z-index: 2;
}

section {
  padding: 6rem 0;
}

section h2 {
  font-size: 2.25rem;
  line-height: 2rem;
}

section h2 span {
  color: #fd1a16;
}

.btn-social {
  width: 3.25rem;
  height: 3.25rem;
  font-size: 1.25rem;
  line-height: 2rem;
}

.tools {
  font-size: 0.9rem;
}

.design,
.dev {
  -ms-transform: skewY(-12deg);
  -webkit-transform: skewY(-12deg);
  transform: skewY(-12deg);
}

.designImg,
.devImg {
  width: 90%;
}

.progress-bar {
  background-color: #888;
}

#Experience {
  padding: 2rem 0;
  border-top: 2px dashed #ddd;
}

footer {
  padding-top: 5rem;
  padding-bottom: 5rem;
  background-color: #999;
  color: #fff;
}

/* MOBILE */
@media (max-width: 767px) {
  .App-logo {
    display: block;
    margin: 0 auto;
  }

  #Experience {
    overflow-x: scroll;
  }

  #Experience .container {
    width: 270%;
  }

  .sub-logo {
    left: 1rem;
  }

  .menu {
    right: 1rem;
  }

  nav {
    right: 2rem;
  }
}

@media (min-width: 992px) {
  header h1 {
    font-size: 3.2em;
    line-height: 3.5rem;
  }

  .profileImg,
  .profileImg img {
    width: 240px;
    transition: all 0.25s ease;
  }

  .profileImg:hover .img1 {
    margin-top: 20px;
    margin-left: 50px;
    -ms-transform: skewY(-15deg);
    -webkit-transform: skewY(-15deg);
    transform: skewY(-15deg);
  }

  .profileImg:hover .img2 {
    -ms-transform: skewY(-15deg);
    -webkit-transform: skewY(-15deg);
    transform: skewY(-15deg);
  }

  .profileImg:hover .img3 {
    margin-left: -50px;
    -ms-transform: skewY(-15deg);
    -webkit-transform: skewY(-15deg);
    transform: skewY(-15deg);
  }

  .designImg,
  .devImg {
    transition: all 0.4s ease;
  }

  .designImg:hover {
    -ms-transform: translateY(-40px);
    -webkit-transform: translateY(-40px);
    transform: translateY(-40px);
  }

  .devImg:hover {
    -ms-transform: scale(1.2);
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }

  section h2 {
    font-size: 3rem;
    line-height: 2.5rem;
  }
}